 .dropdown:hover .dropdown-content {\n            display: block;\n        }\n        /* Change the background color of the dropdown button when the dropdown content is shown */\n        .dropdown:hover .dropbtn {\n            background-color: $hover;\n        }\n        \n        /*.close:hover,\n        .close:focus {\n            color: black;\n            text-decoration: none;\n            cursor: pointer;\n        }*/   \n\n        h1 {\n            margin-left: 0.5rem;\n            color: $title;\n            font-family: Arial, Helvetica, sans-serif;\n            font-weight: 900;\n            font-size: 22pt;\n        }\n\n        .real-center {\n            margin-top: 0.15rem;\n        }\n\n        /*.show {\n            padding-right: 17px;\n            display: block;\n        }*/\n\n        h1:before {\n            color: white;\n            content: \"dofus\";\n        }\n\n        .search-input-global {\n            position: relative;\n            color: white;\n            background-color: #343a40;\n            padding: 6px 20px;\n            margin: 8px 10px;\n            border-color: Transparent;\n            outline-color: #ffd800;\n        }\n\n        .img-size-countrys {\n            width: 32px;\n            height: 32px;\n        }\n\n        ::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */\n            color: white;\n            opacity: 1; /* Firefox */\n          }\n          \n        ::-ms-input-placeholder { /* Internet Explorer 10-11 */\n            color: white;\n        }\n        \n        ::-ms-input-placeholder { /* Microsoft Edge */\n            color: white;\n        }\n\n        /*span {\n            color: $title;\n        }\n\n        .link-title {\n            text-decoration: none;\n        }*/\n\n        ul {\n            list-style: none;\n            display: flex;\n            margin: 0;\n\n            li {\n                //a {\n                    //padding: 10px 20px;\n                    //min-height